export default {
    name: 'app-clerk-historys',
    components: {},
    props: {
        isShow: {
            type: Boolean,
            default: false
        },
        userCardId: {
            type: Number,
            default: 0,
        }
    },
    watch: {
        isShow(newVal) {
            if (!newVal) {
                this.visible = false;
            }

            if (newVal) {
                this.page = 1;
                this.list = [];
                this.getList();
            }
        }
    },
    data() {
        return {
            visible: false,
            list: [],
            page: 1,
            isMore: false,
        }
    },
    methods: {
        close() {
            this.$emit('update:isShow', false);
        },
        getList() {
            let that = this;
            that.$showLoading({
                text: '加载中...'
            });
            that.$request({
                url: that.$api.card.history,
                data: {
                    user_card_id: that.userCardId,
                    page: that.page,
                },
            }).then(response => {
                that.$hideLoading();
                that.visible = true;
                if (response.code === 0) {
                    that.list = that.list.concat(response.data.list);
                    that.page = response.data.list.length > 0 ? that.page + 1 : that.page;
                    if (response.data.list.length === 0) {
                        that.isMore = true;
                    }
                } else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 2000,
                    });
                }
            }).catch(() => {
                that.$hideLoading();
            });
        },
        lower() {
            this.getList();
        }
    }
}
